'use client';

import Link from 'next/link';
import { motion } from 'framer-motion';
import {
  Mail,
  Linkedin,
  FileText,
  BookOpen,
  ScrollText,
  PenLine,
  Send,
  Newspaper,
  Globe,
  File,
  ArrowRight,
  Trophy,
  Package,
  ShieldCheck,
} from 'lucide-react';
import { GlassPanel } from '@/components/ui/GlassPanel';
import { VerticalBadge, StatusBadge } from '@/components/ui/Badge';
import { VERTICAL_COLORS } from '@/lib/constants';
import type { AssetSummary, ContentType } from '@/lib/types';

interface RecentWinsProps {
  assets: AssetSummary[];
}

const contentTypeIcons: Record<ContentType, typeof Mail> = {
  Email: Mail,
  LinkedIn: Linkedin,
  Brochure: FileText,
  CaseStudy: BookOpen,
  Whitepaper: ScrollText,
  BlogPost: PenLine,
  SalesEmail: Send,
  Newsletter: Newspaper,
  WebPage: Globe,
  Unknown: File,
};

const containerVariants = {
  hidden: { opacity: 0 },
  show: {
    opacity: 1,
    transition: {
      staggerChildren: 0.1,
    },
  },
};

const itemVariants = {
  hidden: { opacity: 0, y: 20 },
  show: { opacity: 1, y: 0 },
};

export function RecentWins({ assets }: RecentWinsProps) {
  return (
    <GlassPanel className="p-6 h-full">
      {/* Header */}
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center gap-2">
          <Trophy className="w-5 h-5 text-yellow-400" />
          <h2 className="text-lg font-semibold text-white">Recent Wins</h2>
        </div>
        <Link
          href="/vault"
          className="text-sm text-tsi-blue-light hover:text-white transition-colors flex items-center gap-1"
        >
          View all
          <ArrowRight className="w-4 h-4" />
        </Link>
      </div>

      {/* Asset Grid */}
      <motion.div
        className="grid gap-3"
        variants={containerVariants}
        initial="hidden"
        animate="show"
      >
        {assets.length === 0 ? (
          <div className="text-center py-8">
            <Trophy className="w-10 h-10 text-white/20 mx-auto mb-3" />
            <p className="text-white/60 text-sm">No recent assets</p>
          </div>
        ) : (
          assets.slice(0, 4).map((asset) => {
            const Icon = contentTypeIcons[asset.contentType];
            const color = VERTICAL_COLORS[asset.vertical];

            return (
              <motion.div
                key={asset.slug}
                variants={itemVariants}
              >
                <Link href={`/vault?asset=${asset.slug}`}>
                  <div className="glass-card p-4 group cursor-pointer">
                    <div className="flex items-start gap-3">
                      {/* Icon */}
                      <div
                        className="w-10 h-10 rounded-lg flex items-center justify-center flex-shrink-0 transition-transform group-hover:scale-110"
                        style={{ backgroundColor: `${color}20` }}
                      >
                        <Icon className="w-5 h-5" style={{ color }} />
                      </div>

                      {/* Content */}
                      <div className="flex-1 min-w-0">
                        <p className="text-sm font-medium text-white line-clamp-2 group-hover:text-tsi-blue-light transition-colors">
                          {asset.title}
                        </p>
                        <div className="flex items-center gap-2 mt-2">
                          <VerticalBadge vertical={asset.vertical} />
                          <StatusBadge status={asset.status} />
                        </div>
                      </div>

                      {/* Arrow */}
                      <ArrowRight className="w-4 h-4 text-white/30 group-hover:text-white/60 transition-colors flex-shrink-0 mt-1" />
                    </div>
                  </div>
                </Link>
              </motion.div>
            );
          })
        )}
      </motion.div>
    </GlassPanel>
  );
}
