'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { motion, AnimatePresence } from 'framer-motion';
import { Activity, RefreshCw, CheckCircle2, AlertCircle, AlertTriangle, Info } from 'lucide-react';
import { GlassPanel } from '@/components/ui/GlassPanel';
import { VERTICAL_COLORS } from '@/lib/constants';
import { formatDistanceToNow } from 'date-fns';
import type { AutopilotAction } from '@/lib/types';

interface LiveFeedProps {
  initialActions: AutopilotAction[];
}

const statusIcons = {
  success: CheckCircle2,
  error: AlertCircle,
  warning: AlertTriangle,
  info: Info,
};

const statusColors = {
  success: 'text-green-400',
  error: 'text-red-400',
  warning: 'text-yellow-400',
  info: 'text-tsi-blue-light',
};

export function LiveFeed({ initialActions }: LiveFeedProps) {
  const router = useRouter();
  const [actions, setActions] = useState(initialActions);
  const [isRefreshing, setIsRefreshing] = useState(false);

  // Auto-refresh every 30 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      router.refresh();
    }, 30000);

    return () => clearInterval(interval);
  }, [router]);

  // Update actions when initialActions changes (from server refresh)
  useEffect(() => {
    setActions(initialActions);
  }, [initialActions]);

  const handleManualRefresh = async () => {
    setIsRefreshing(true);
    router.refresh();
    setTimeout(() => setIsRefreshing(false), 1000);
  };

  return (
    <GlassPanel className="p-6 h-full">
      {/* Header */}
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center gap-2">
          <Activity className="w-5 h-5 text-tsi-blue-light" />
          <h2 className="text-lg font-semibold text-white">Live Feed</h2>
        </div>
        <motion.button
          onClick={handleManualRefresh}
          className="p-2 rounded-lg hover:bg-white/10 transition-colors"
          whileTap={{ scale: 0.95 }}
        >
          <RefreshCw
            className={`w-4 h-4 text-white/60 ${isRefreshing ? 'animate-spin' : ''}`}
          />
        </motion.button>
      </div>

      {/* Feed List */}
      <div className="space-y-1 max-h-[400px] overflow-y-auto scrollbar-hide pr-2">
        <AnimatePresence initial={false}>
          {actions.length === 0 ? (
            <div className="text-center py-8">
              <Activity className="w-10 h-10 text-white/20 mx-auto mb-3" />
              <p className="text-white/60 text-sm">No recent activity</p>
            </div>
          ) : (
            actions.map((action, index) => {
              const Icon = statusIcons[action.status];
              const verticalColor = action.vertical
                ? VERTICAL_COLORS[action.vertical]
                : '#6B7280';

              return (
                <motion.div
                  key={`${action.timestamp}-${index}`}
                  initial={{ opacity: 0, x: -20 }}
                  animate={{ opacity: 1, x: 0 }}
                  exit={{ opacity: 0, x: 20 }}
                  transition={{ delay: index * 0.05 }}
                  className="flex items-start gap-3 p-3 rounded-lg hover:bg-white/5 transition-colors group"
                >
                  {/* Vertical Dot */}
                  <div className="flex-shrink-0 mt-1">
                    <div
                      className="w-2 h-2 rounded-full"
                      style={{ backgroundColor: verticalColor }}
                    />
                  </div>

                  {/* Content */}
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center gap-2">
                      <Icon className={`w-4 h-4 ${statusColors[action.status]}`} />
                      <span className="text-sm font-medium text-white">
                        {action.action}
                      </span>
                      {action.vertical && (
                        <span
                          className="text-xs px-1.5 py-0.5 rounded"
                          style={{
                            backgroundColor: `${verticalColor}20`,
                            color: verticalColor,
                          }}
                        >
                          {action.vertical}
                        </span>
                      )}
                    </div>
                    <p className="text-xs text-white/60 mt-0.5 line-clamp-2">
                      {action.details}
                    </p>
                  </div>

                  {/* Timestamp */}
                  <div className="flex-shrink-0 text-xs text-white/40">
                    {formatRelativeTime(action.timestamp)}
                  </div>
                </motion.div>
              );
            })
          )}
        </AnimatePresence>
      </div>

      {/* Live Indicator */}
      <div className="mt-4 pt-4 border-t border-white/10 flex items-center justify-center gap-2">
        <div className="relative">
          <div className="w-2 h-2 rounded-full bg-green-500" />
          <div className="absolute inset-0 w-2 h-2 rounded-full bg-green-500 animate-ping" />
        </div>
        <span className="text-xs text-white/50">Auto-refreshing every 30s</span>
      </div>
    </GlassPanel>
  );
}

function formatRelativeTime(timestamp: string): string {
  try {
    // Parse "2026-01-21 05:31:03" format
    const date = new Date(timestamp.replace(' ', 'T'));
    return formatDistanceToNow(date, { addSuffix: true });
  } catch {
    return timestamp;
  }
}
