'use client';

import { motion } from 'framer-motion';
import { Activity, CheckCircle2, AlertTriangle, XCircle } from 'lucide-react';
import { GlassPanel } from '@/components/ui/GlassPanel';
import { HEALTH_CONFIG } from '@/lib/constants';
import type { SystemHealth } from '@/lib/types';

interface HealthOrbProps {
  health: SystemHealth;
}

const statusIcons = {
  GREEN: CheckCircle2,
  YELLOW: AlertTriangle,
  RED: XCircle,
};

export function HealthOrb({ health }: HealthOrbProps) {
  const config = HEALTH_CONFIG[health.status];
  const Icon = statusIcons[health.status];

  // CSS variables for the orb colors
  const orbStyle = {
    '--orb-color-light': health.status === 'GREEN' ? '#22c55e' : health.status === 'YELLOW' ? '#eab308' : '#ef4444',
    '--orb-color-dark': health.status === 'GREEN' ? '#15803d' : health.status === 'YELLOW' ? '#a16207' : '#b91c1c',
    '--orb-glow': health.status === 'GREEN' ? 'rgba(34, 197, 94, 0.4)' : health.status === 'YELLOW' ? 'rgba(234, 179, 8, 0.4)' : 'rgba(239, 68, 68, 0.4)',
  } as React.CSSProperties;

  return (
    <GlassPanel className="p-6 h-full">
      <div className="flex flex-col items-center justify-center h-full min-h-[300px]">
        {/* Orb Container */}
        <div className="relative mb-6">
          {/* Glow Effect */}
          <motion.div
            className="absolute inset-0 rounded-full blur-2xl opacity-50"
            style={{
              background: `radial-gradient(circle, ${orbStyle['--orb-glow']}, transparent 70%)`,
            }}
            animate={{
              scale: [1, 1.1, 1],
              opacity: [0.3, 0.5, 0.3],
            }}
            transition={{
              duration: 3,
              repeat: Infinity,
              ease: 'easeInOut',
            }}
          />

          {/* Main Orb */}
          <motion.div
            className="health-orb"
            style={orbStyle}
            initial={{ scale: 0.8, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            transition={{ duration: 0.5 }}
          >
            {/* Inner Icon */}
            <div className="absolute inset-0 flex items-center justify-center">
              <Icon className="w-12 h-12 text-white/90 drop-shadow-lg" />
            </div>
          </motion.div>
        </div>

        {/* Status Text */}
        <motion.div
          className="text-center"
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3 }}
        >
          <h2 className={`text-2xl font-bold ${config.color}`}>
            {config.label}
          </h2>
          <p className="text-white/60 mt-1 text-sm max-w-[200px]">
            {health.message}
          </p>
        </motion.div>

        {/* Health Details */}
        {health.details && (
          <motion.div
            className="mt-6 w-full"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{ delay: 0.5 }}
          >
            <div className="grid grid-cols-2 gap-3">
              <div className="bg-white/5 rounded-lg p-3 text-center">
                <p className="text-2xl font-bold text-white">
                  {health.details.agentsOnline}
                  <span className="text-white/40 text-lg">/{health.details.totalAgents}</span>
                </p>
                <p className="text-xs text-white/50 mt-1">Agents Online</p>
              </div>
              <div className="bg-white/5 rounded-lg p-3 text-center">
                <p className="text-2xl font-bold text-white">
                  {health.details.publishQueueCount}
                </p>
                <p className="text-xs text-white/50 mt-1">Queued Items</p>
              </div>
            </div>
          </motion.div>
        )}

        {/* Last Check */}
        <p className="text-xs text-white/40 mt-4 flex items-center gap-1">
          <Activity className="w-3 h-3" />
          Last checked: {health.lastCheck}
        </p>
      </div>
    </GlassPanel>
  );
}
